.site-header {
  background-color: #161c36;
  color: #ffffff;
  position: relative;
}
.site-header--scroll {
  background-color: #ffffff;
}
.site-header--scroll .site-header__logo--opened {
  display: block;
}
.site-header--scroll .site-header__logo--closed {
  display: none;
}
.site-header--scroll .site-header__toggle {
  background-color: #cccccc;
}
.site-header--scroll .site-header__toggle::before {
  background-color: #cccccc;
}
.site-header--opened {
  background-color: #ffffff;
}
.site-header--opened .site-header__toggle {
  display: none;
}
.site-header--opened .site-header__logo--opened {
  display: none;
}
.site-header--opened .site-header__logo--closed {
  display: block;
}
.site-header--active {
  background-color: #ffffff;
}
.site-header--active .site-header__toggle {
  background-color: #cccccc;
  transform: rotate(45deg);
}
.site-header--active .site-header__toggle::before {
  background-color: #cccccc;
  top: 0;
  transform: rotate(-90deg);
}
.site-header--active .site-header__logo--opened {
  display: block;
}
.site-header--active .site-header__logo--closed {
  display: none;
}
.site-header__wrapper {
  padding: 27px 25px 23px 25px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 270px;
}
.site-header__logo--opened {
  display: none;
}
.site-header__login {
  display: none;
  font-size: 17px;
  line-height: 30px;
  font-weight: bold;
  background-color: #ffd74b;
  color: #192144;
}
.site-header__toggle {
  position: relative;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  border: none;
  border-radius: 1px;
  margin-top: 3px;
  outline: none;
  transition: 0.2s ease-out;
}
.site-header__toggle::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 3px;
  top: 8px;
  right: 0px;
  background-color: #ffffff;
  border-radius: 1px;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .site-header {
    background-color: #182044;
  }
  .site-header--active {
    background-color: #ffffff;
  }
  .inner-page .site-header {
    background-color: #161c35;
  }
  .site-header__wrapper {
    padding: 40px;
    width: 688px;
  }
  .site-header__toggle {
    margin-top: 20px;
    margin-left: 25px;
  }
  .site-header__login {
    display: block;
    font-size: 20px;
    line-height: 30px;
    font-weight: bold;
    text-transform: uppercase;
    width: 188px;
    text-align: center;
    padding: 10px 0 10px 0;
    border-radius: 25px;
  }
}
@media (min-width: 1440px) {
  .site-header__wrapper {
    padding: 50px 60px 10px 60px;
    width: 1320px;
  }
  .site-header__logo {
    height: 50px;
  }
  .site-header__toggle {
    width: 60px;
    margin-top: 25px;
    pointer-events: none;
  }
  .site-header__toggle::before {
    display: none;
  }
  .site-header__login {
    display: none;
  }
}
