.filter {
  background-color: #e2e5f2;
  position: relative;
  height: 35px;
}
.filter::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  left: 0;
  top: 100%;
  background-color: #e2e5f2;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}
.filter::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  left: 0;
  bottom: 100%;
  background-color: #e2e5f2;
  z-index: -1;
}
.filter--closed .filter__continent {
  display: none;
}
.filter--closed .filter__button::after {
  display: none;
}
.filter--closed .filter__country {
  display: none;
}
.filter--opened .filter__continent {
  display: block;
}
.filter--opened .filter__button::before {
  display: none;
}
.filter__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
